
.fixed-top-custom{
	width: 100%;
	height: 70px;
	opacity: 0.9;
	background-color: #ebebeb;
}
.nav-buttons{
	text-align: right;
}
h1{
	color: #007bff;
}
.proManager{
	text-align: center;
	width: 110px;
	height: 120px;
	background-color: #3dce5c;
	border-radius: 0 0 10% 10%;
	color: white;
	-webkit-text-decoration-line: none;
	        text-decoration-line: none;
}
.logo:hover{
	-webkit-text-decoration-line: none;
	        text-decoration-line: none;
}
.proManager:hover{
	color: #3dce5c;
	background-color: #ebebeb;
	-webkit-text-decoration-line: none;
	        text-decoration-line: none;
}

#logoff{
	color: black;
}

#logoff:hover{
	-webkit-text-decoration-line: none;
	        text-decoration-line: none;
}

.btn-primary{
	background-color: #3dce5c;
	border: 1px solid #3dce5c;	
}

.btn-primary:hover{
	color: #3dce5c;	
	border: 1px solid #3dce5c;	
	background-color: white;	
}
.btn-primary:click{
	color: #3dce5c;	
	border: 1px solid #3dce5c;	
	background-color: white;	
}



